
import React, { Component } from 'react';
import { Msg } from "react-weui";

import Title from '../components/Title'

export default (props) => (
  <div>
    <Msg
        type="success"
        title=""
        description=""
        ></Msg>
    <div style={{textAlign : "center"}}>
      <img src={props.proviewImg} style={{"width" : "90%"}} />
    </div>
  </div>
)



